@import url('https://fonts.googleapis.com/css?family=Lora|Oxygen:700|Source+Sans+Pro:300,600');

$screen-xs-max: "767px";
$screen-sm-min: "768px";
$screen-sm-max: "991px";
$screen-md-min: "992px";
$screen-md-max: "1199x";
$screen-lg-min: "1200px";

@mixin content_padding() {
	@media (max-width: $screen-xs-max) {
		padding: 60px 0;
	}

	@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
		padding: 75px 0;
	}
	
	@media (min-width: $screen-md-min) and (max-width: $screen-md-max) {
		padding: 80px 0;
	}

	@media (min-width: $screen-lg-min) {
		padding: 100px 0;
	}
}

@mixin content_padding_alt() {
	@media (max-width: $screen-xs-max) {
		padding: 60px 0;
	}

	@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
		padding: 65px 0;
	}
	
	@media (min-width: $screen-md-min) and (max-width: $screen-md-max) {
		padding: 70px 0;
	}

	@media (min-width: $screen-lg-min) {
		padding: 80px 0;
	}
}

// MEDIA QUERIES
// --------------------------------------------------
@mixin mq($property, $mobile, $tabletup:"", $tablet:"", $smallup:"", $small:"", $large:"") {
	
	/* Only mobile devices */
	@if ($mobile) {
		@media (max-width: $screen-xs-max) {
			#{$property}: #{$mobile}; 
		}
	}

	/* Small devices (tablets, 768px and up) */
	@if ($tabletup != null) {
		@media (min-width: $screen-sm-min) {
			#{$property}: #{$tabletup}; 
		}
	}

	/* Only tablet devices */
	@if ($tablet != null) {
		@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
			#{$property}: #{$tablet};
		}
	}

	/* Medium devices and up*/
	@if ($smallup != null) {
		@media (min-width: $screen-md-min) {
			#{$property}: #{$smallup};	
		}
	}

	/* Only medium devices */
	@if ($small != null) {
		@media (min-width: $screen-md-min) and (max-width: $screen-md-max) {
			#{$property}: #{$small};
		}
	}

	@if ($large != null) {
		@media (min-width: $screen-lg-min) {
			#{$property}: #{$large};	
		}
	}
}

* {
	text-decoration: none !important;
}

html, body {
	min-width: 320px;
	font-smoothing: antialiased;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
	font-family: 'Source Sans Pro', sans-serif;
	background: $athens;
	line-height: 1.7;
	color: $content;
	letter-spacing: 0.8px;
}

p {
	margin-bottom: 30px;
	@include mq(font-size, "15px",'',"15px",'',"16px","16px");

	&:last-of-type {
		margin-bottom: 0 !important;
	}
}

ul {
	p ~ & {
		margin-bottom: 60px;
	}
}

h1, h2, h3, h4 {
	font-family: 'Lora', serif;
	margin-bottom: 30px;
	line-height: 1.3;

	&:first-oftype {
		margin-bottom: 0 !important;
	}
}

h1 {
	@include mq(font-size, "29px",'',"35px",'',"40px","45px");
}

h2 {
	@include mq(font-size, "19px",'',"20px",'',"25px","30px");
}

h3, h4 {
	@include mq(font-size, "19px",'',"20px",'',"25px","20px");
}

.container, .col-xs-12, .row {
	@media (max-width: $screen-xs-max) {
		&.mobile {
			margin: 0 !important;
			padding: 0 !important;
		}
	}
}

.align {
	display: table;
	width: 100%;
	height: 100%;

	.align-inner {
		display: table-cell;
		vertical-align: middle;
	}
}

.meta {
	font-family: 'Oxygen', sans-serif;
	text-tranform: uppercase;
}

@mixin stack() {
	list-style: none;
	margin: 0;
	padding: 0;
}

@mixin clear() {
	&:after {
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}
}

blockquote {
	font-family: 'Lora', serif;
	@include mq(font-size, "25px",'',"26px",'',"27px","30px");

	@media (max-width: $screen-xs-max) {
		width: 100%;
		text-align: center;
	}

	@media (min-width: $screen-sm-min) {
		max-width: 300px;
	}

	&.right {
		margin-bottom: 40px;
		margin-top: 40px;

		@media (min-width: $screen-sm-min) {
			margin-left: 40px;
			float: right;
		}
	}

	&.left {
		float: left;
		margin-top: 40px;
		magrin-bottom: 40px;

		@media (min-width: $screen-sm-min) {
			margin-right: 40px;
		}
	}
}

.highlight {
	background-image: linear-gradient(180deg,$primary 65%, $primary 0);
    background-size: 100%;
    padding: 2px 5px;
    background-repeat: no-repeat;
}

hr {
	&.divider {
		border-top: 1px solid $content;
		margin: 40px 0;
	}
}

.overlay {
	display: none;
	-webkit-animation: fade 500ms;
	animation: fade 500ms;
	background-color: rgba(0,0,0,0);

	&.active {
	    display: block;
	    position: fixed;
	    top: 0;
	    right: 0;
	    bottom: 0;
	    left: 0;
	    z-index: 9998;
	    background-color: rgba(0,0,0,0.5);
	    -webkit-animation: fade 500ms;
	    animation: fade 500ms;
    }
}

#overflow-menu {
	background: white;
	position: fixed;
    width: 300px;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 9999;
    overflow: hidden;
    visibility: hidden;
    -webkit-overflow-scrolling: touch;
	
	-webkit-transform: translate3d(-300px, 0, 0);
    transform: translate3d(-300px, 0, 0);			

	-webkit-transition: -webkit-transform 400ms cubic-bezier(0.16, 0.68, 0.43, 0.99);
	transition: transform 400ms cubic-bezier(0.16, 0.68, 0.43, 0.99);

	&.open {
		-webkit-transform: translate3d(0, 0, 0);
		transform: translate3d(0, 0, 0);
		visibility: visible;
		-webkit-transition: -webkit-transform 400ms cubic-bezier(0.16, 0.68, 0.43, 0.99);
		transition: transform 400ms cubic-bezier(0.16, 0.68, 0.43, 0.99);
	}

	.inner {
		overflow-y: scroll;
		padding: 30px;

		ul {
			@include clear();
			@include stack();
		}
	}
}

@mixin animation() {
	-webkit-transition: all .2s ease-in-out;
	-o-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out;
}

@mixin global_border_radius() {
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	border-radius: 4px;
}

@mixin calc($property, $expression) {
  #{$property}: -moz-calc(#{$expression}) !important;
  #{$property}: -webkit-calc(#{$expression}) !important;
  #{$property}: calc(#{$expression}) !important;
}

.container {
	padding-left: 30px;
    padding-right: 30px;
}

.row {
	margin-left: -30px;
    margin-right: -30px;
}

.col-lg-1, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-10, .col-lg-11, .col-lg-12, .col-md-1, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-10, .col-md-11, .col-md-12, .col-sm-1, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11, .col-sm-12, .col-xs-1, .col-xs-2, .col-xs-3, .col-xs-4, .col-xs-5, .col-xs-6, .col-xs-7, .col-xs-8, .col-xs-9, .col-xs-10, .col-xs-11, .col-xs-12 {
	padding-left: 30px;
    padding-right: 30px;
}