#hero {
	height: 100vh;
}

#related {
	@include content_padding();
	text-align: center;

	.card {
		margin-bottom: 0 !important;
	}
}

#body {
	background: white;
	@include content_padding();

	&.center {
		text-align: center;
	}
}

.panel {
	@include content_padding();

	&.center {
		text-align: center;
	}
}

header.page-header {
	background: $athens;
	@include content_padding();
	text-align: center;

	h1 {
		margin: 0;
	}
}

.time {	
	@media (max-width: $screen-xs-max) {
		margin-right: 30px;
	}

	@media (min-width: $screen-sm-min) {
		margin-right: 60px;
	}
}

.card {
	margin-bottom: 30px;

	h2 {
		margin-bottom: 15px !important;

		@media (max-width: $screen-xs-max) {
			padding: 0 30px;
		}

		@media (min-width: $screen-sm-min) {
			padding: 0 20px;
		}
	}

	h2, p {
		color: $content;
	}
}

.image-container {
	position: relative;
	margin-bottom: 30px;
	text-align: center;

	img {
		width: 100%;
		height: auto;
	}
}

.category-align {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
}

.meta-data {
	font-size: 13px;
	color: $content;
}

a.meta-block {
	cursor: pointer;
	@include animation();

	&:hover {
		border-bottom: 2px solid $primary !important;
		@include animation();
	}
}

.meta-block {
	font-family: 'Oxygen', sans-serif;
	text-transform: uppercase;
	font-size: 10px;
	display: inline-block;
	background: white;
	padding: 5px 11px;
	color: $content !important;
}