#main_logo {
	display: inline-block;
	vertical-align: middle;

	@media (max-width: $screen-xs-max) {
		width: 174px;
		height: 55px;
	}

	@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
		width: 231px;
		height: 73px;
	}

	@media (min-width: $screen-md-min) and (max-width: $screen-md-max) {
		width: 264px;
		height: 83px;
	}

	@media (min-width: $screen-lg-min) {
		width: 333px;
		height: 105px;
	}
}

#header {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	padding: 20px 0;

	#menu_toggle {
		position: relative;
		z-index: 2;
		margin-top: 33px;
		-webkit-transition: margin-top ease 350ms;
		transition: margin-top ease 350ms;
		cursor: pointer;
		height: 13px;
		display: inline-block;
		vertical-align: middle;
		margin-right: 30px;

		&:hover {
			span {
				@include animation();

				background: $primary;

				&:before, &:after {
					background: $primary;
					@include animation();
				}
			}
		}

		span {	
			cursor: pointer;
			top: 6px;
			height: 2px;
			width: 25px;
			background-color: $content;
			display: block;
			content: "";
			position: relative;
			@include animation();

			&:before {
				height: 2px;
				width: 25px;
				background-color: $content;
				display: block;
				content: "";
				top: -6px;
				position: relative;
				@include animation();
				cursor: pointer;
			}

			&:after {
				top: 4px;
				height: 2px;
				width: 25px;
				background-color: $content;
				display: block;
				content: "";
				position: relative;
				@include animation();
				cursor: pointer;
			}
		}
	}
}